body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1f262a;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about {
  color: white;
  background-color: #1f262a;
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
}

.about h1 {
  font-size: 40px;
  margin: 20px;
}

.about .row #intro {
  font-size: 20px;
  color: white;
  text-align: center;
  padding: 40px;
  padding-top: 0px;
  padding-bottom: 0px;
}

.blinkerfix {
  display: inline;
}

.header {
  position: relative;
  top: -40px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}
.typed-cursor {
  font-size: 40px;
}

.strikeout {
  font-size: 2em;
  line-height: 1em;
  position: relative;
}
.strikeout::after {
  border-bottom: 0.07em solid#c9a3b8;
  content: "";
  left: 0;
  margin-top: calc(0.125em / 2 * -1);
  position: absolute;
  z-index: 10;
  right: 0;
  top: 60%;
}

.title {
  display: inline-block;
  color: #1f262a;
  font-size: 30px;
  top: -150px;
}
.titlebox {
  position: relative;
}

.perlin {
  position: relative;
  margin: 0px;
  padding: 0px;
  border-width: 0;
}

.hide {
  overflow-y: hidden;
  overflow-x: hidden;
  overflow: hidden;
}
.mobileNote {
  height: 80px;
  padding: 20px;
  margin-top: 20px;
  margin: 40px;
  font-size: 20px;
  font-weight: italic;
}

ul.social-media-list img {
  padding: 5px;
  border-radius: 5px;
  background-color: #1f262a;
  width: 36px;
  height: 36px;
}

ul.social-media-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

ul.social-media-list li {
  display: inline-block;
  margin: 2px;
}

ul.social-media-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 0px; /* eliminate space between li */
}
